.container{
    max-width: 1200px;
    width: 100vw;
    height: auto;
    min-height: 80vh;
    display: grid;
    grid-template-columns: 30vw auto;
    
    justify-content: space-evenly;
}
.boxtext{
    margin-top: 10vh;
    
}
.boxtext h2{
    font-size: 2em;
    margin-bottom: 4vh;
}
.boxtext p{
    font-size: 0.9em;
}
.boxtext button{
    padding: 10px;
    width: 15vw;
    margin-top: 4vh;
    background-color: rgb(243, 159, 42);
    border-radius: 10px;
}
div img{
    width: 500px;
}
@media only screen and (max-width: 1170px) {
    div img{
        width: 600px;
    }

}
@media only screen and (max-width: 970px) {
    div img{
        margin: auto;
        width: 500px;
    }
    .container{
        display: grid;
        grid-template-columns:auto;
        grid-template-rows: auto auto;
        width: 40vw;
        margin: auto;
    }
    .conteinerImg{
        margin: auto;
    }
    .boxtext button{
        padding: 10px;
        width: 30vw;
        margin-bottom: 10px;
        
    }

}
@media (max-width: 540px) {
    
    .boxtext{
        width: 90vw;
        margin: auto;
        
        
    }
    .boxtext button{
        padding: 10px;
        width: 30vw;
        margin-bottom: 10px;   
    }
    .boxtext h2{
        font-size: 1.8em;
        margin-bottom: 4vh;
    }
    .boxtext p{
        font-size: 0.9em;
    }
    .boxtext button{
        width: 35vw;

    }
    div img{
        margin: auto;
        width: 400px;
    }

}
