
.shop{
    width: 100vw;
    height: 100%;
    overflow-x: hidden;
    
}
.shoptitle{
    display: grid;
    justify-content: center;
    padding: 25px;
    
}
.ShopText{
    font-size: 3em;
    font-weight: 800;
}
.Products{
    margin-top: 50px;
    width: 90vw;
    height: auto;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;
    
    
}
.product{
    padding: 50px;
}
.Products img{
    width: 300px;
    margin-bottom: 50px;
}
.Products .product{
    text-align: center;
}
.product:hover{
    transition: 0.3s ease-in;
    cursor: pointer;
}
.add{
    padding: 10px;
    border: solid black 2px;
    background-color: white;
    color: black;
    border-radius: 50px;
}
@media only screen and (max-width: 1250px) {
    .Products{
       
        display: grid;
        grid-template-columns: 1fr 1fr;

    }
  }
  @media only screen and (max-width: 850px) {
    .Products{
       
        display: grid;
        grid-template-columns: 1fr;

    }
  }
  @media only screen and (max-width: 470px) {
    .Products{
       
        display: grid;
        grid-template-columns: 1fr;

    }
    .Products{
        margin-top: 50px;
        width: 100vw;
    }
    .ShopText{
        font-size: 2em;
    }
    .Products img{
        width: 250px;
        margin-bottom: 50px;
    }
  }
  @media only screen and (max-width: 360px) {
    
    .Products{
        margin-top: 30px;
        width: 100vw;
    }
    .ShopText{
        font-size: 2em;
        padding: 5px;
        width: 100vw;
        text-align: center;

    }
    .Products img{
        width: 200px;
        margin-bottom: 50px;
    }

  }