:root{
  --background: rgb(0, 0, 0);
}

*{
  padding: 0px;
  margin: 0px;
  border: 0px;
  box-sizing: border-box;
  font-family: 'Open Sans','sans-sefif';

}
html, body, #root{
  -webkit-scroll-behavior: smooth;
  overflow-x: hidden;
  
}
.nav2{
  display: none;
}
@media (max-width: 1004px) { 
  .nav1{
    display: none;
  }
  .nav2{
      height: 10vh;
      display: flex;
  }
  
  .tt h1{
      font-size: 6em;
  }
  .tt p{
      font-size: 1em;
      font-weight: 400
  }
  .tt div button{
      margin: 0px;
      width: 30vw;
      font-size: 0.8em;
  }
  .none{
      display: none;
  }
}
@media (max-width: 390px) { 
  
  
  .tt h1{
      font-size: 4.5em;
  }
  .tt p{
      font-size: 0.8em;
      font-weight: 400
  }
  .tt div button{
      margin: 0px;
      padding: 5px;
      width: 30vw;
      font-size: 0.7em;
  }
}
@media (max-height: 400px) { 
  
  
  .tt h1{
      font-size: 4.5em;
  }
  .tt p{
      font-size: 0.8em;
      font-weight: 400
  }
  .tt div button{
      margin: 0px;
      padding: 5px;
      width: 30vw;
      font-size: 0.7em;
  }
}
@media (max-height: 370px) { 
  
  
  .tt h1{
      font-size: 3em;
  }
  .tt p{
      font-size: 0.8em;
      font-weight: 400
  }
  .tt div button{
      margin: 0px;
      padding: 5px;
      width: 30vw;
      font-size: 0.7em;
  }
}
