.navbar {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.link{
    padding: 4vh;
    text-decoration: none;
    color: black;
    margin-right: 0px;
}
.logo{
    font-size: 2em;
    margin-left:5vw;
}
@media (max-width: 480px) {
    
    .logo{
        font-size: 1.5em;
        margin-left:2vw;
    }
    .link{
        padding: 0vh;
        margin-right: 2vw;
    }

}