.cart{
    width: 100vw;
    height: auto;
    min-height: 90vh;
    display: grid;
    justify-content: center;
}
.cartTitle{
    padding: 25px;
    font-weight: 800;
}
.CartItems{
    max-width: 1000px;
    width: 80vw;
    height: 20vh;
    margin-bottom: 40px;
    box-shadow: 1px 2px 12px #a9a5a5;
    display: flex;
    justify-content: space-around;
    border-radius: 10px;
    align-items: center;
}
.detalhes{
    max-width: 25vw;
}
.contImg{
    margin-left: 10px;
    width: 30vw;
   
}
.CartItems img{
    width: auto;
    height: 17vh;
}
.CartItems p{
    margin-right: 30px;
    font-size: 1.4em;
    text-align: center;
}
.contador p button{
    
    width: 35px;
    height: 40px;
}

.contador p input{
    width: 7vw;
    text-align: center;
    border: 1px solid white;
}
.checkout{
    text-align: center;
    margin: 10px;
    width: 70vw;
}
.checkout button{
    background-color: black;
    color: white;
    padding: 20px;
    margin-left: 45px;
    border-radius: 10px;
    margin-top: 25px;
}
.erro{
    display: flex;
    justify-content: center;
    align-items: start;
}
@media only screen and (max-width: 570px) {
    .CartItems{
        max-width: 1000px;
        width: 100vw;
        height: 30vh;
        margin-bottom: 40px;
        box-shadow: 1px 2px 12px #ffffff;
        display: flex;
        justify-content: space-around;
        border-radius: 10px;
        align-items: center;
    }
    .detalhes{
        display: grid;
        justify-content: center;
        width: 40vw;
    }
    .CartItems p{
        margin-right: 0px;
        font-size: 1.4em;
    }
    .contador p button{
    
        width: 20px;
        height: 40px;
    }
    
    .contador p input{
        width: 7vw;
        text-align: center;
        border: 1px solid white;
    }
  }
@media only screen and (max-width: 400px) {
    .CartItems{
        
        height: 35vh;
        
    }
    .CartItems img{
        width: auto;
        height: 10vh;
    }
    .cartTitle {
        padding: 25px;
        font-weight: 800;
        font-size: 1.5em;
        text-align: center;
    }
}
@media only screen and (max-height: 500px) {
    .CartItems {
        
        margin-bottom: 60px;
        
    }
    .CartItems{
        
       
        box-shadow: 1px 2px 12px #ffffff;
        
    }
}
@media (min-width: 800px) and (max-height: 800px) {
    .CartItems {
        
        height: 30vh;
        
    }
}
@media (min-width: 800px) and (max-height: 550px) {
    .CartItems {
        
        height: 35vh;
        
    }
}
@media (min-width: 600px) and (max-height: 550px) {
    .CartItems {
        
        height: 45vh;
        
    }
}